h1,
h2,
h3,
p,
ul {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
h2 {
  text-align: center;
  font-family: var(--main-font-family);
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  vertical-align: top;
  color: var(--main-color);
}
p {
  font-family: var(--main-font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  vertical-align: top;
  color: var(--main-color);
}
body {
  background-color: var(--bg-color);
  color: var(--main-color);
  font-family: var(--main-font-family);
}
.container {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
  @media screen and (min-width: $tablet) and (max-width: $pre-desctop) {
    padding-right: 32px;
    padding-left: 32px;
  }
  @media screen and (min-width: $desctop) {
    padding-right: 16px;
    padding-left: 16px;
  }
}
